div#welcome > h2 {
  font-size: 12px;
}

.welcome {
  margin-top: 20px;
}

.night .welcome {
  background-color: transparent;
}

/* Retro Browser Styles */
.browser-viewport div#home {
  background: linear-gradient(135deg, #ffe6f0 0%, #e6f3ff 100%);
  font-family: 'Times New Roman', serif;
  position: relative;
}

/* Grid pattern overlay */
.browser-viewport div#home::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image:
    repeating-linear-gradient(
      0deg,
      transparent,
      transparent 35px,
      rgba(150, 150, 255, 0.25) 35px,
      rgba(150, 150, 255, 0.25) 36px
    ),
    repeating-linear-gradient(
      90deg,
      transparent,
      transparent 35px,
      rgba(255, 150, 150, 0.2) 35px,
      rgba(255, 150, 150, 0.2) 36px
    );
  pointer-events: none;
}

.browser-viewport div#home .page-content h1 {
  text-align: center;
  color: #ff0000;
  font-family: 'Comic Sans MS', 'Arial', sans-serif;
  text-decoration: underline;
  animation: rainbow 3s linear infinite;
}

@keyframes rainbow {
  0% {
    color: #ff0000;
  }
  16% {
    color: #ff8800;
  }
  33% {
    color: #ffff00;
  }
  50% {
    color: #00ff00;
  }
  66% {
    color: #0088ff;
  }
  83% {
    color: #8800ff;
  }
  100% {
    color: #ff0000;
  }
}

.browser-viewport div#home .welcome {
  border: 3px ridge #c0c0c0;
  background: linear-gradient(135deg, #ffffcc, #ccffff);
  margin: 20px auto;
  text-align: center;
}

.browser-viewport div#home .page-content p {
  font-size: 14px;
  color: #000080;
}

/* Under Construction Footer */
.under-construction-footer {
  text-align: center;
  margin-top: 30px;
  padding: 10px;
  color: #666;
  font-size: 11px;
  font-family: 'Courier New', monospace;
}

/* Override clip-path when inside browser window */
.browser-viewport .welcome {
  clip-path: none;
  background-color: #ffffcc;
  border: 2px dashed #ff00ff;
  padding: 15px;
  margin: 10px 0;
}

.browser-viewport .welcome.secondary {
  background-color: #ccffff;
  border: 2px dashed #0088ff;
}
